html {
  font-size: 62.5%; }

body {
  font-size: 1.6rem;
  font-family: "Noto Sans JP", sans-serif;
  background-color: #f6f8fa; }

a {
  text-decoration: none; }

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto; }

.header {
  height: 6.4rem;
  display: flex;
  align-items: center;
  background-color: #682444; }

.nav {
  padding: 0 2.4rem; }
  .nav .logo {
    display: flex;
    color: #FFF;
    font-size: 3rem; }

.upload {
  background-color: #FFF;
  border-radius: 1rem;
  padding: 3.2rem;
  margin-top: 1.6rem;
  border: 1px solid #e1e4e8; }
  .upload h2 {
    margin-top: 0; }
  .upload .field {
    margin: 3.2rem 0; }

.select-input {
  position: relative;
  display: flex;
  align-items: center;
  border: 1px solid #e1e4e8;
  border-radius: 1rem;
  background-color: #fafbfc;
  color: #0366d6;
  width: 100%;
  height: 4.8rem;
  padding-left: 1.6rem;
  cursor: pointer; }
  .select-input::after {
    content: "";
    position: absolute;
    top: 2.08rem;
    right: 1.6rem;
    border: 0.53333rem solid;
    border-color: #676f77 transparent transparent transparent;
    cursor: pointer;
    transition: 0.5s ease-in-out; }
  .select-input:hover, .select-input.selected {
    background-color: #f3f4f6; }

.month-container {
  display: grid;
  grid-template-columns: repeat(4, 4.8rem);
  grid-template-rows: repeat(3, 4.8rem);
  border: 1px solid #e1e4e8;
  border-radius: 1rem;
  background-color: #f3f4f6;
  width: fit-content;
  margin: 0 auto 0 auto;
  height: 0;
  overflow: hidden;
  opacity: 0.01;
  transition: 0.5s; }
  .month-container input {
    width: 1px;
    height: 1px;
    display: inline-block;
    position: absolute;
    opacity: 0.01; }
  .month-container label {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer; }
    .month-container label:nth-of-type(1) {
      border-top-left-radius: 1rem; }
    .month-container label:nth-of-type(4) {
      border-top-right-radius: 1rem; }
    .month-container label:nth-of-type(9) {
      border-bottom-left-radius: 1rem; }
    .month-container label:nth-of-type(12) {
      border-bottom-right-radius: 1rem; }
    .month-container label:hover {
      background-color: #0366d6;
      color: #FFF; }
  .month-container input:checked + label {
    background-color: #0366d6;
    color: #FFF; }

.select-input.selected + .month-container {
  margin-top: 0.8rem;
  opacity: 1;
  height: 14.4rem; }

.select-input.selected::after {
  transform: rotate(180deg);
  top: 1.54667rem; }
